.bodyContainer {
  display: flex;
  flex: 1;
  width: 100%;
  min-height: 100vh;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
  margin: 0;
}

.container {
  display: flex;
  flex: 1;
  width: 100%;
  max-width: none;
}

.wrapper {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: flex-start;
  gap: 20px;
  padding: 25px 15px;
  box-sizing: border-box;
}
